.tf-btn-log {
    margin-bottom: 10px;
}

.tf-img-has-border img {
    border: 2px solid #040404;
}

.tf-mdl-image {
    cursor: pointer;
    max-width: 100%;
    margin-bottom: 3px;
    &.tf-mdl-image-active {
        border: 2px solid #646464;
        padding: 2px;
    }
}

.tf-i2 {
    box-sizing: border-box;
    clear: both;
}

figure.tf-i2,
.tf-i2 figure {
    padding: 0 !important;
    margin: 0 !important;
}

.wp-block[data-align=left] .tf-i2 {
    margin-right: 15px !important;
}

.wp-block[data-align=right] .tf-i2 {
    margin-left: 15px !important;
}

.wp-block[data-align=center] .tf-i2 {
    margin-left: auto !important;
    margin-right: auto !important;
}

//i2-image-css
.tf-i2-image {
    figure {
        margin: 0 !important;
    }
}

.tf-i2-azon-image {
    .components-resizable-box__container {
        img {
            width: 100% !important;
        }
    }
}

.tf-text-align-end {
    text-align: right;
    text-align: end;
}

//tf-components
.tf-card {
    position: relative;
    .tf-card-button {
        background-color: #fff;
        top: 10px;
        position: absolute;
        left: 10px;
    }
    .tf-button-minus.components-button {
        color: rgb(153, 0, 0);
        box-shadow: inset 0 0 0 1px rgb(153, 0, 0);
        &:hover {
            color: rgb(153, 0, 0);
            box-shadow: inset 0 0 0 1px rgb(153, 0, 0);
            background-color: rgba(153, 0, 0, .3);
        }
    }
    .tf-button-trash {
        left: auto;
        right: 10px;
    }
    .tf-card-image {
        height: 250px;
        display: flex;
        align-items: center;
        img {
            margin: 0 auto;
        }
    }
    .tf-card-devider {
        margin-top: 5px;
        margin-bottom: 5px;
    }
}

.tf-flex-sf-container {
    align-items: flex-end;
    margin-bottom: 4px;
    >div {
        flex: 0 1 33.33%;
    }
}

.components-button {
    &.tf-full-button {
        align-items: center;
        display: flex;
        justify-content: center !important;
        width: 100%;
    }
}

.tf-top-pagination-wrapper {
    nav {
        justify-content: flex-end;
    }
}

.editor-post-featured-image {
    .no-custom-image {
        display: inline-block;
        position: absolute;
        background: #fff;
        bottom: 40px;
        left: 9px;
    }
    .clear-custom-image {
        color: rgb(153, 0, 0);
        background: #fff;
        position: absolute;
        bottom: 90px;
        left: 9px;
    }
}

.tf-amz-image-wrapper {
    display: flex;
    flex-direction: column;
    .no-custom-image {
        display: inline-block;
        position: absolute;
        background: #fff;
        bottom: 40px;
        left: 9px;
    }
    .clear-custom-image {
        color: rgb(153, 0, 0);
        background: #fff;
        position: absolute;
        bottom: 90px;
        left: 9px;
    }
    .components-button {
        padding: 0px 0px;
    }
    .editor-post-featured-image {
        img {
            width: 100%!important;
        }
    }
}

.tf-table {
    border-collapse: separate;
    .tf-td {
        position: relative;
    }
    .block-editor-rich-text__editable {
        margin: 3px;
        border: 1px solid #eeeeee;
    }
    .components-base-control__field,
    .components-base-control__help {
        margin-bottom: 0;
    }
    .AZIMG {
        vertical-align: top !important;
        .editor-post-featured-image__toggle {
            min-height: 36px;
            line-height: 15px;
            margin-top: 5px;
            font-size: 13px;
        }
    }
}

.tf-group-five {
    align-items: start;
    .tf-input {
        max-width: 20%;
        padding: 0;
        margin: 0 !important;
        input {
            padding: 3px 1px 3px 5px !important;
            min-height: 20px;
        }
    }
}

.tf-item-align-left,
.tf-item-align-right,
.tf-item-align-center {
    display: flex;
}

.tf-item-align-left {
    justify-content: flex-start;
}

.tf-item-align-right {
    justify-content: flex-end;
}

.tf-item-align-center {
    justify-content: center;
}

.tf-item-width-full {
    width: 100%;
}

.tf-item-width-three-fourth {
    width: 75%;
}

.tf-item-width-half {
    width: 50%;
}

.tf-btn-3d {
    padding: 1px 0 13px 0;
    background-color: #e47911;
    border-bottom: 4px solid #693a02;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: .2px;
    border-radius: 6px;
    transition: .2s linear;
    &:hover {
        opacity: .75;
        box-shadow: rgba(3, 3, 3, .4) 0 8px 12px 0!important;
    }
}

.tf-btn-rounded {
    border-radius: 10px;
}

.my-color-picker button.is-link {
    padding: 0px 5px;
    margin-bottom: 3px;
    font-size: 11px;
    border-radius: 2px;
    text-decoration: none;
    white-space: nowrap;
    color: #0075af;
    border: 1px solid #0075af;
    background: #f3f5f6;
    &:hover,
    &:active {
        background: #f1f1f1;
        border-color: #005d8c;
        color: #005d8c;
    }
}